.lanyard-wrapper {
  position: absolute;
  z-index: 20;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  transform: scale(1);
  transform-origin: center;
  background-color: transparent;
  top: -5vh;
  left: 0px;
  z-index: 1000
}

#mousePointer-ball {
  width: 40px;
  height: 40px;
  background-color: rgb(245, 245, 245, 0.1);
  position: absolute;
  border-radius: 50px;
  transition: 0s ease-in-out;
  pointer-events: none;
  z-index: 10;
  backdrop-filter: blur(5px);
  border: 4px solid #007AFF;
}

.shape {
  width: 100vw;
  height: 100vh;
  background: #31465e;
  clip-path: polygon(90% 100%, 90% 50%, 10% 50%, 10% 0, 100% 0%, 100% 100%);
  border-radius: 50px;
  position: absolute;
  top: 0px;
  left: 0px;
  z-index: -100;
}
