

#stepper-wrapper {
  width: calc(100vw - 20px);
  height: calc(100vh - 20px);

  background-color: transparent;

  position: relative;
  top: 10px;
  left: 10px
}

#stepper-body {
  width: 100%;
  height: calc(40vw - 10px);
  max-width: 700px;
  min-height: 250px;
  max-height: 400px;

  background-image: linear-gradient(
    135deg,
    hsl(211deg 31% 28%) 0%,
    hsl(211deg 31% 27%) 0%,
    hsl(211deg 31% 26%) 0%,
    hsl(211deg 30% 26%) 2%,
    hsl(211deg 30% 25%) 6%,
    hsl(211deg 29% 24%) 15%,
    hsl(212deg 29% 23%) 55%,
    hsl(212deg 28% 22%) 72%,
    hsl(212deg 27% 21%) 81%,
    hsl(213deg 26% 20%) 88%,
    hsl(213deg 25% 19%) 93%,
    hsl(214deg 23% 18%) 97%,
    hsl(215deg 20% 16%) 100%
  );

  box-shadow: 0px 10px 25px -5px rgb(49, 71, 94, 0.2);

  border-radius: 20px;

  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  overflow: hidden
}

.stepper-body-child {
  width: calc(100% - 20px);
  height: calc(100% - 20px);

  background-color: transparent;

  padding: 10px;

  transition: 0.5s;
  position: absolute;
  left:0%
}
.stepper-body-child.hide {
  width: calc(100% - 20px);
  height: calc(100% - 20px);

  background-color: transparent;

  padding: 10px;

  transition: 0.5s;
  position: absolute;
  opacity: 0;
  left: 100%;
}

.stepper-body-child input[type= 'text'] {
  width: calc(100% - 20px - 20px);
  height: 50px;
  max-width: 350px;
  
  font-size: 1.2rem;
  color: #f5f5f5;
  font-family: 'GSansMedium';

  background-color: rgb(33, 40, 50, 0.1);

  border: 1px solid rgb(245, 245, 245, 0.5);
  border-radius: 10px;
  outline: none;

  padding: 0px 5px 0px 10px;

  position: absolute;
  top: 50%;
  left:50%;
  transform: translate(-50%, -50%);
}

.stepper-body-child textarea {
  width: calc(100% - 20px - 20px);
  height: 50%;
  max-width: 350px;
  resize: none;
  
  font-size: 1.2rem;
  color: #f5f5f5;
  font-family: 'GSansRegular';

  background-color: rgb(33, 40, 50, 0.1);

  border: 1px solid rgb(245, 245, 245, 0.5);
  border-radius: 10px;
  outline: none;

  padding: 5px 5px 0px 10px;

  position: absolute;
  top: 50%;
  left:50%;
  transform: translate(-50%, -50%);
}

textarea::-webkit-scrollbar {
  display: none;
}

.stepper-child-title {
  font-size: 1.7rem;
  font-family: 'GSansBold';
  color: rgb(245, 245, 245, 1)
}

.stepper-child-caption {
  font-size: 1.2rem;
  font-family: 'GSansRegular';

  color: rgb(245, 245, 245, 1);
  margin-top: 10px
}

.stepper-child-next {
  width: max-content;
  height: 40px;
  
  padding: 0px 20px 0px 20px;

  font-size: 1.3rem;
  font-family: 'GSansBold';

  color: rgb(245, 245, 245, 1);
  margin-top: 10px;

  background-color: #007AFF;

  border: none;
  border-radius: 35px;

  cursor: pointer;

  position: absolute;
  bottom: 10px;
  right: 10px
}

.stepper-child-prev {
  width: max-content;
  height: 40px;
  
  padding: 0px 20px 0px 20px;

  font-size: 1rem;
  font-family: 'GSansRegular';

  color: rgb(245, 245, 245, 1);
  margin-top: 10px;

  background-color: transparent;

  border: none;
  border-radius: 35px;

  cursor: pointer;

  position: absolute;
  bottom: 10px;
  left: 10px
}

@media screen and (min-width: 1170px) {
  #stepper-body {
    width: 100%;
    height: calc(40vw - 10px);
    max-width: 600px;
    min-height: 250px;
    max-height: 350px;
  
    background-image: linear-gradient(
      135deg,
      hsl(211deg 31% 28%) 0%,
      hsl(211deg 31% 27%) 0%,
      hsl(211deg 31% 26%) 0%,
      hsl(211deg 30% 26%) 2%,
      hsl(211deg 30% 25%) 6%,
      hsl(211deg 29% 24%) 15%,
      hsl(212deg 29% 23%) 55%,
      hsl(212deg 28% 22%) 72%,
      hsl(212deg 27% 21%) 81%,
      hsl(213deg 26% 20%) 88%,
      hsl(213deg 25% 19%) 93%,
      hsl(214deg 23% 18%) 97%,
      hsl(215deg 20% 16%) 100%
    );
  
    box-shadow: 0px 10px 25px -5px rgb(49, 71, 94, 0.2);
  
    border-radius: 40px;
  
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  
    overflow: hidden
  }

  .stepper-body-child {
    width: calc(100% - 40px);
    height: calc(100% - 40px);
  
    background-color: transparent;
  
    padding: 20px
  }

  .stepper-child-title {
    font-size: 2rem;
    font-family: 'GSansBold';
    color: rgb(245, 245, 245, 1)
  }
  
  .stepper-child-caption {
    font-size: 1.2rem;
    font-family: 'GSansRegular';
  
    color: rgb(245, 245, 245, 1);
    margin-top: 10px
  }
  
  .stepper-child-next {
    width: max-content;
    height: 40px;
    
    padding: 0px 30px 0px 30px;
  
    font-size: 1.3rem;
    font-family: 'GSansBold';
  
    color: rgb(245, 245, 245, 1);
    margin-top: 10px;
  
    background-color: #007AFF;
  
    border: none;
    border-radius: 35px;
  
    cursor: pointer;
  
    position: absolute;
    bottom: 20px;
    right: 20px
  }
  .stepper-child-prev {
    width: max-content;
    height: 40px;
    
    padding: 0px 20px 0px 20px;
  
    font-size: 1.1rem;
    font-family: 'GSansRegular';
  
    color: rgb(245, 245, 245, 0.5);
    margin-top: 10px;
  
    background-color: transparent;
  
    border: none;
    border-radius: 35px;
  
    cursor: pointer;
  
    position: absolute;
    bottom: 20px;
    left: 20px;
    transition: 0.3s;
  }
  .stepper-child-prev:hover{
    color: rgb(245, 245, 245, 1);
  }
}