html {
  background-color: #212832;
}

#htmlWrapper {
  position: fixed;
  width: 100%;
  height: 100vh;
  top: 0px;
  left: 0px;
  scroll-snap-type: y mandatory;
  scroll-behavior: smooth;
  gap: 30px;
  justify-content: start;
  overflow-y: scroll;
  overflow-x: hidden;
  transition: 2s;
}

#htmlWrapper::-webkit-scrollbar {
  display: none;
}

.maindiv {
  width: 100vw;
  height: 100vh;

  position: relative;
  top: 0px;
  left: 0px;

  scroll-snap-align: center;
  scroll-snap-stop: always;
  transition: 2s;
}

#mainDiv1-desktop {
  display: none;
}
#mainDiv1-mobile {
  display: block;
}
#mainDiv1-mobile-fadeblur {
  background-color: transparent;
  width: 100%;
  height: 100%;
  position: absolute;
}


#mainDiv-2_wrapper {
  width: calc(100% - 20px);
  height: calc(100% - 20px);

  top: 10px;
  left: 10px;
  position: absolute;

  background-color: transparent;
}

#mainDiv-2_iridescent {
  width: 100%;
  height: 30%;
  max-height: 300px;
  min-height: 100px;

  overflow: hidden;
  background-color: transparent;
  border-radius: 25px;
  transition: 0.6s;
}

#mainDiv-2_iridescent-wrapper {
  width: 100%;
  height: 30%;
  max-height: 300px;
  min-height: 100px;

  background-color: transparent;

  position: absolute;
}

#mainDiv-2_iridescent-about {
  width: calc(100% - 40px);
  height: 30%;

  background-color: rgb(33, 40, 50, 0.3);
  backdrop-filter: blur(20px);

  position: absolute;
  top: 10px;
  right: 10px;

  display: flex;
  overflow: hidden;
  place-items: center;
  padding-left: 20px;
  text-align: right;

  border-radius: 15px;

  font-size: 2rem;
  font-family: 'GSansBold';
  color: #F5F5F5;
}

#mainDiv-2_iridescent-button-wrapper {
  width: 70px;
  height: 70px;

  background-color: rgb(33, 40, 50, 0);

  position: absolute;
  bottom: 10px;
  left: 10px;
  
  border-radius: 80px;

}

#mainDiv-2_iridescent-button {
  width: 70px;
  height: 70px;

  background-color: rgb(33, 40, 50, 0.3);
  backdrop-filter: blur(20px);

  position: relative;
  bottom: 0px;
  left: 0px;
  
  border-radius: 80px;

  display: grid;
  place-items: center;
  align-items: center;

  user-select: none;
}

#mainDiv-2_iridescent-button img {
  width: 60px;
  height: 60px;
}

#mainDiv-2_self-intro {
  width: 100%;
  height: calc(100% - 30% - 10px);

  background-color: transparent;

  position: relative;
  top: 10px;
}

#self-intro_journal {
  width: calc(100% - 10px);
  height: calc(30% - 10px);

  background-color: transparent;

  border: 5px solid #31475E;
  border-radius: 25px;

  overflow-x: auto;
  display: flex;
  flex-direction: row;
  gap: 10px;
  justify-content: start;
  scroll-snap-type: x mandatory;
  scroll-behavior: smooth;

  position: relative;
}
#self-intro_journal::-webkit-scrollbar {
  display: none;
}

.self-intro_journal-child {
  min-width: calc(100% - 10px - 20px);
  height: calc(100% - 10px - 20px);

  background-image: linear-gradient(
    135deg,
    hsl(211deg 31% 28%) 0%,
    hsl(211deg 31% 27%) 0%,
    hsl(211deg 31% 26%) 0%,
    hsl(211deg 30% 26%) 2%,
    hsl(211deg 30% 25%) 6%,
    hsl(211deg 29% 24%) 15%,
    hsl(212deg 29% 23%) 55%,
    hsl(212deg 28% 22%) 72%,
    hsl(212deg 27% 21%) 81%,
    hsl(213deg 26% 20%) 88%,
    hsl(213deg 25% 19%) 93%,
    hsl(214deg 23% 18%) 97%,
    hsl(215deg 20% 16%) 100%
  );

  border-radius: calc(25px - 5px - 5px);
  margin-top: 5px;
  scroll-snap-align: center;
  scroll-snap-stop: always;
  scroll-behavior: smooth;

  padding: 10px;

  font-size: 1.2rem;
  font-family: 'GSansMedium';
  color: #F5F5F5;

  display: grid;
  align-items: center;
  place-items: center;
  place-content: center;
  text-align: center;
}

.self-intro_journal-child:first-child {
  margin-left: 5px;
}
.self-intro_journal-child:last-child {
  margin-right: 5px;
}

#self-intro_about {
  width: 100%;
  height: calc(100% - 30% - 10px);

  position: relative;
  top: 10px;

  background-color: transparent;
}

#self-intro_about--mobile {
  width: 100%;
  height: 100%;

  position: relative;
  top: 0px;

  background-image: linear-gradient(
    135deg,
    hsl(211deg 31% 28%) 0%,
    hsl(211deg 31% 27%) 0%,
    hsl(211deg 31% 26%) 0%,
    hsl(211deg 30% 26%) 2%,
    hsl(211deg 30% 25%) 6%,
    hsl(211deg 29% 24%) 15%,
    hsl(212deg 29% 23%) 55%,
    hsl(212deg 28% 22%) 72%,
    hsl(212deg 27% 21%) 81%,
    hsl(213deg 26% 20%) 88%,
    hsl(213deg 25% 19%) 93%,
    hsl(214deg 23% 18%) 97%,
    hsl(215deg 20% 16%) 100%
  );

  border-radius: 25px;
}

#self-intro_about--mobile-image{
  width: calc(100% - 20px);
  height: calc(100% - 20px);

  position: absolute;
  top: 10px;
  left: 10px;
}

#self-intro_about--mobile img {
  width: 100%;
  height: 100%;

  position: absolute;

  background-image: url('../../resource/Lucas.jpg');
  background-size: 101%;
  background-position-y: bottom;
  background-position-x: center;

  overflow: show;
  border: none;

  border-radius: 15px;
}


#self-intro_about--mobile-carousel {
  width: calc(100% - 20px);
  height: calc(100% - 20px);

  position: absolute;
  top: 10px;
  left: 10px;

  background-color: transparent;

  overflow-x: scroll;
  display: flex;
  flex-direction: row;
  gap: 0px;
  justify-content: start;
  scroll-snap-type: x mandatory;
  scroll-behavior: smooth;

  border: none;

  border-radius: 15px;
}

#self-intro_about--mobile-carousel::-webkit-scrollbar{
  display: none;
}

.self-intro_about--mobile-carouselChild {
  min-width: 100%;
  height: 100%;

  background-color: transparent;

  overflow: show;
  border: none;

  border-radius: 15px;

  scroll-snap-align: center;
  scroll-snap-stop: always;
}
.self-intro_about--mobile-carouselChild:last-child {
  min-width: calc(100% - 20px);
  height: calc(100% - 10px);

  background-color: rgb(33, 40, 50, 0.3);
  backdrop-filter: blur(30px);

  overflow: show;
  border: none;

  border-radius: 15px;
  
  scroll-snap-align: center;
  scroll-snap-stop: always;

  padding: 10px 10px 0px 10px;

  font-size: 1.2rem;
  font-family: 'GSansRegular';
  color: #F5F5F5
}

#self-intro_about--desktop {
  display: none;
}


@media screen and (min-width: 1170px) {
  #mainDiv1-desktop {
    width: 100vw;
    height: 100vh;

    position: absolute;

    display: block;
  }
  #mainDiv1-mobile {
    display: none;
  }
  #self-intro_about--mobile {
    display: none;
  }
  #self-intro_about--desktop {
    display: block;
  }

  #mainDiv-1_NAME {
    /*Font*/
    font-family: 'GSansRegular';
    color: rgb(245, 245, 245, 0.5);

    /*Position*/
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 15rem
  }


  #mainDiv-2_wrapper {
    width: calc(100% - 40px);
    height: calc(100% - 40px);

    top: 20px;
    left: 20px;
    position: absolute;

    background-color: transparent;
  }

  #mainDiv-2_iridescent {
    width: 78%;
    height: 100%;
    max-height: unset;
    min-height: unset;

    overflow: hidden;
    background-color: transparent;
    border-radius: 40px;

    transition: 0.6s;
  }

  #mainDiv-2_iridescent-wrapper {
    width: 78%;
    height: 100%;
    max-height: unset;
    min-height: unset;

    background-color: transparent;

    position: absolute;
  }

  #mainDiv-2_iridescent-about {
    width: 45%;
    height: 13%;
    min-width: 650px;
    max-height: 120px;
    min-height: 100px;

    background-color: rgb(33, 40, 50, 0.3);
    backdrop-filter: blur(40px);

    position: absolute;
    top: unset;
    bottom: 20px;
    right: 20px;

    display: flex;
    overflow: hidden;
    place-items: center;
    padding-left: 20px;
    text-align: right;

    border-radius: 20px;

    font-size: 4rem;
    font-family: 'GSansBold';
    color: #F5F5F5;
  }

  #mainDiv-2_iridescent-button-wrapper {
    width: 100px;
    height: 100px;

    background-color: rgb(33, 40, 50, 0);

    position: absolute;
    bottom: 20px;
    left: 20px;
    
    border-radius: 80px;

  }

  #mainDiv-2_iridescent-button {
    width: 100px;
    height: 100px;

    background-color: rgb(33, 40, 50, 0.3);
    backdrop-filter: blur(40px);

    position: relative;
    bottom: 0px;
    left: 0px;
    
    border-radius: 80px;

    display: grid;
    place-items: center;
    align-items: center;

    user-select: none;
  } #mainDiv-2_iridescent-button img {
    width: 80px;
    height: 80px;
  }


  #mainDiv-2_self-intro {
    width: calc(100% - 78% - 20px);
    height: 100%;
  
    background-color: transparent;
  
    position: absolute;
    top: 0px;
    right: 0px
  }

  #self-intro_journal {
    width: calc(100% - 12px);
    height: calc(30% - 12px);
  
    background-color: transparent;
  
    border: 6px solid #31475E;
    border-radius: 40px;
  
    overflow-x: auto;
    display: flex;
    flex-direction: row;
    gap: 10px;
    justify-content: start;
    scroll-snap-type: x mandatory;
    scroll-behavior: smooth;
  
    position: relative;
  }
  
  .self-intro_journal-child {
    min-width: calc(100% - 12px - 20px);
    height: calc(100% - 12px - 20px);
  
    background-image: linear-gradient(
      135deg,
      hsl(211deg 31% 28%) 0%,
      hsl(211deg 31% 27%) 0%,
      hsl(211deg 31% 26%) 0%,
      hsl(211deg 30% 26%) 2%,
      hsl(211deg 30% 25%) 6%,
      hsl(211deg 29% 24%) 15%,
      hsl(212deg 29% 23%) 55%,
      hsl(212deg 28% 22%) 72%,
      hsl(212deg 27% 21%) 81%,
      hsl(213deg 26% 20%) 88%,
      hsl(213deg 25% 19%) 93%,
      hsl(214deg 23% 18%) 97%,
      hsl(215deg 20% 16%) 100%
    );
  
    border-radius: calc(40px - 6px - 6px);
    margin-top: 6px;
    scroll-snap-align: center;
    scroll-snap-stop: always;

    padding: 10px;

    font-size: 2rem;
    font-family: 'GSansMedium';
    color: #F5F5F5;

    display: grid;
    align-items: center;
    place-items: center;
    place-content: center;
  }
  .self-intro_journal-child:first-child {
    margin-left: 6px;
  }
  .self-intro_journal-child:last-child {
    margin-right: 6px;
  }


  #self-intro_about--desktop {
    width: 100%;
    height: 100%;
  
    position: relative;
    top: 0px;
  
    background-image: linear-gradient(
      135deg,
      hsl(211deg 31% 28%) 0%,
      hsl(211deg 31% 27%) 0%,
      hsl(211deg 31% 26%) 0%,
      hsl(211deg 30% 26%) 2%,
      hsl(211deg 30% 25%) 6%,
      hsl(211deg 29% 24%) 15%,
      hsl(212deg 29% 23%) 55%,
      hsl(212deg 28% 22%) 72%,
      hsl(212deg 27% 21%) 81%,
      hsl(213deg 26% 20%) 88%,
      hsl(213deg 25% 19%) 93%,
      hsl(214deg 23% 18%) 97%,
      hsl(215deg 20% 16%) 100%
    );
  
    border-radius: 40px;
  }
  .about_desktop--img {
    width: calc(100% - 20px);
    aspect-ratio: 1/1;

    background-image: url('../../resource/Lucas.jpg');
    background-size: cover;
    background-position-x: center;
    background-position-y: bottom;

    border-radius: 30px;

    position: relative;
    top: 10px;
    left: 10px;
  }

  #about_desktop--caption {
    width: calc(100% - 20px);



    position: relative;
    left: 10px;
    top: calc(20px + 5px);

    font-size: 1.25rem;
    font-family: 'GSansRegular';
    color: #F5F5F5;
    letter-spacing: 1.1px;
  }
}